@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Fredericka+the+Great&family=Henny+Penny&display=swap);
*{margin:0;padding:0;box-sizing:border-box}ul,ol,li{list-style:none}a{text-decoration:none}body{background:#c9c0f5}figure{width:100%;height:100vh;position:relative;overflow:hidden}figure h1{font:normal italic 60px/1 "fredericka the great";color:#222;position:absolute;top:5vh;left:7vw;z-index:2}figure .cameraBtns{position:absolute;top:7vh;right:10vw;display:flex;z-index:3}figure .cameraBtns li{margin-left:60px;font:italic 22px/1 "Architects Daughter";color:#555;cursor:pointer;transition:.5s}figure .cameraBtns li.on{-webkit-transform:scale(1.2);transform:scale(1.2);color:#333;font-weight:bold}figure .txt article{position:absolute;z-index:-1;opacity:1}figure .txt article h2{font:normal italic 9vw/1 "Henny Penny";color:#222}figure .txt article h2 span{display:inline-block;opacity:0;-webkit-transform:scale(3);transform:scale(3);transition-duration:.5s;transition-timing-function:cubic-bezier(0.48, -0.69, 0.53, 1.59)}figure .txt article p{font:14px/1.4 "arial";color:#555;position:absolute;z-index:2;transition:1s 1s;opacity:0;-webkit-transform:translateX(100px);transform:translateX(100px)}figure .txt article.on{z-index:2}figure .txt article.on h2 span{opacity:1;-webkit-transform:scale(1);transform:scale(1)}figure .txt article.on p{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}figure .txt article:nth-of-type(1){right:10vw;top:30vh}figure .txt article:nth-of-type(1) p{width:26vw;margin-top:20px;margin-left:100px}figure .txt article:nth-of-type(2){right:7vw;top:24vh}figure .txt article:nth-of-type(2) p{width:18vw;margin-top:50px;margin-left:20px}figure .txt article:nth-of-type(3){left:7vw;bottom:50vh}figure .txt article:nth-of-type(3) p{width:26vw;margin-top:20px;margin-left:0px}figure .txt article:nth-of-type(4){left:12vw;bottom:18vh}figure .txt article:nth-of-type(4) p{width:36vw;margin-top:-12vw;margin-left:0px}figure .txt article:nth-of-type(5){left:10vw;top:33vh}figure .txt article:nth-of-type(5) p{width:20vw;margin-top:50px;margin-left:-10px}figure .txt article:nth-of-type(6){right:10vw;top:30vh}figure .txt article:nth-of-type(6) p{margin-top:50px;margin-left:30px}figure .colorPicker{position:absolute;bottom:10vh;right:10vw;z-index:2;display:flex}figure .colorPicker li{width:100px;height:10px;border:1px solid #222;cursor:pointer}
